<template>
  <div class="wrapper">
    <Header @search="search = $event" />
    <main>
      <Notes :notes="filterNotes" @delTask="delTask" @editTask="editTask"/>
    </main>
    <Teleport to="#body">
         <Transition name="modal">
             <Modal v-show="isShowModal" @closeModal="isShowModal = false" @inpVal="modalTitle = $event" @areaVal="modalBody = $event" @addOrChange="addOrChange" :title="modalTitle" :body="modalBody" :id="curID"/>
          </Transition>
    </Teleport>

    <span class="open-modal" @click="isShowModal = true, modalTitle='', modalBody='', curID = null">
      <img src="./assets/img/icons/add-note.svg" alt="">
    </span>
  </div>
</template>

<script>

import Header from './components/common/Header.vue';
import Notes from './components/notes/Notes.vue';
import Modal from './components/UI/Modal.vue';

export default {
  name: 'App',
  components: {
    Header,
    Notes,
    Modal
  },
  data() {
    return{
      notes: [
        {
          id: 1,
          title: "Task 1",
          body: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Eveniet cumque nostrum accusantium reiciendis. Debitis, assumenda exercitationem? Voluptates adipisci dolore magni.",
          time: "15.07.2023",
        },
        {
          id: 2,
          title: "Task 2",
          body: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Eveniet cumque nostrum accusantium reiciendis. Debitis, assumenda exercitationem? Voluptates adipisci dolore magni.",
          time: "15.01.2021",
        },
        {
          id: 3,
          title: "Task 3",
          body: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Eveniet cumque nostrum accusantium reiciendis. Debitis, assumenda exercitationem? Voluptates adipisci dolore magni.",
          time: "16.12.2017",
        },
      ],
      isShowModal: false,
      modalTitle: "",
      modalBody: "",
      cardID: null,
      search: "",
    }
  },
  methods: {
    addOrChange(id){
      let obj = {
        id: new Date().getTime(),
        title: this.modalTitle,
        body: this.modalBody,
        time: new Date(Date.now()).toLocaleDateString()
      }
      if (id) {
        let idx = this.notes.findIndex(item => item.id == id)
        this.notes[idx] = obj
      } else {
        this.notes.push(obj)
      }
      this.isShowModal = false
      this.modalTitle = ""
      this.modalBody = ""
      this.isShowModal = false
    },
    delTask(id) {
      let idx = this.notes.findIndex((item) => item.id == id)
      this.notes.splice(idx, 1)
    },
    editTask(id) {
      this.isShowModal = true
      let editID = this.notes.find(item => item.id == id)
      this.modalTitle = editID.title
      this.modalBody = editID.body
      this.curID = editID.id
    }
  },
  computed: {
    filterNotes() {
      return this.search ? this.notes.filter(item => item.title.toLowerCase().includes(this.search.toLowerCase())) : this.notes
    }
  }
}
</script>

